* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  line-height: 1.5;
}
body {
  overflow-x: hidden;
}

/*  --------- NavBar --------- */
/* Copyright 2024 - VikashVerma2 */

nav {
  display: flex;
  justify-content: space-between;
  padding: 0px 8%;
  background: #131d3b;
  color: white;
  align-items: center;
}
.hero-nav ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
}
.hero-nav ul li a {
  display: flex;
  justify-content: space-between;
  color: white;
  text-decoration: none;
  margin: 0px 13px 0px 13px;
  font-size: 18px;
}
.hero-nav ul li a:hover {
  color: #ff4f01;
}
.hero-icons {
  display: flex;
  align-items: center;
}
.hero-icons ul {
  display: flex;
  list-style: none;
  margin-right: 10px;
}
.hero-icons ul li a {
  margin: 8px;
  color: white;
  font-size: 15px;
}
.hero-icons button {
  padding: 15px 40px;
  margin-right: 10px;
  background-color: #ff4f01;
  color: white;
  font-size: 20px;
  cursor: pointer;
}
.hero-icons button:hover {
  background-color: white;
  color: black;
}
.nav2 {
  display: flex;
  justify-content: space-between;
  padding: 5px 8%;
  color: gray;
  align-items: center;
  text-align: center;
}
.clg-logo img {
  width: 500px;
  cursor: pointer;
}
.menu-web {
  display: flex;
  align-items: center;
}
.menu-web ul {
  display: flex;
  list-style: none;
}
.menu-web ul li a {
  display: flex;
  color: #22385d;
  font-size: 18px;
  text-decoration: none;
  margin: 20px;
}
.menu-web ul li a:hover {
  color: #ff4f01;
}
.nav2-img img {
  width: 300px;
  height: 60px;
  cursor: pointer;
}
.ham-menu {
  display: none;
}
.ham-menu button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}

/*  --------- Advisory Board ---------- */
/* Copyright 2024 VikashVerma2 */

.advisory {
  display: block;
  text-align: center;
  line-height: 20px;
  margin: 6%;
}
.advisory h1 {
  font-size: 3rem;
  color: #131d3b;
  font-weight: 900;
}
.advisory .team-para {
  font-size: 1.5rem;
  color: #ff4f01;
  line-height: 1.5rem;
  text-transform: capitalize;
  padding-bottom: 1rem;
}
.advisory-hero {
  display: flex;
  justify-content: center;
  margin-top: 10%;
}
.advisory-containor {
  width: 400px;
  height: 540px;
  display: block;
  background-color: rgb(248, 248, 248);
  margin: 6%;
}
.advisory-img {
  display: flex;
  justify-content: center;
}
.advisory-img img {
  width: 200px;
  height: 240px;
  border-radius: 20%;
  margin: 20px;
}

.advisory-des h2 {
  color: #131d3b;
  font-size: 28px;
  font-weight: 700;
}
.advisory-des p {
  font-size: 20px;
  font-weight: 200;
  margin: 10px 40px;
}

/*  --------- ClgInfo  --------- */
/* Copyright 2024 - VikashVerma2 */

.ClginfoBG {
  width: 100vw;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url(../src/Assets/ClgBackground.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 4% 8%;
  display: flex;
}
.Cloginfo-Containor {
  width: 100%;
}
.Clginfo-title {
  color: orangered;
  font-size: 60px;
  line-height: 25px;
  font-weight: 900;
}
.Clginfo-title-span {
  color: white;
  font-size: 30;
}
.Clginfo-content {
  display: flex;
  align-items: center;
}
.Clginfo-icon img {
  width: 42px;
  height: 42px;
}
.Clginfo-des p {
  color: white;
  font-size: 20px;
  margin-left: 20px;
  padding: 5px 0px;
}
.Clginfo-des p b {
  font-size: 20px;
}

/*  ------------------- About Us --------------- */
/* Copyright 2024 - Vikash Verma */

.AboutUs {
  padding: 5% 6%;
  width: auto;
}

.about-title {
  display: grid;
  align-items: center;
  place-items: center;
  text-align: center;
}
.about-title h3 {
  color: #ff4f01;
  font-size: 24px;
  font-weight: 200;
  text-transform: uppercase;
}
.about-title h1 {
  font-size: 3rem;
  color: #131d3b;
  font-weight: 900;
}

.about-containor {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.about-thumbnail {
  margin: 1rem;
  position: relative;
}
.about-galary img {
  width: 100%;
  height: 260px;
  background-size: cover;
}
.about-galary-des {
  width: 94%;
  height: 90%;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: rgba(21, 21, 42, 0.8);
  color: white;
  opacity: 0;
  transition: ease-out 0.5s;
  margin: 10px;
  padding: 1.5rem;
}
.about-galary-des h2 {
  margin-bottom: 18px;
}
.about-galary-des p {
  font-size: 15px;
  margin-bottom: 10px;
}
.about-galary-des a {
  font-size: 18px;
  color: #ff4f01;
  text-decoration: none;
}
.about-galary-des:hover {
  opacity: 1;
  padding: 1.5rem;
}

/*  ------------------- Our Team --------------- */
/* Copyright 2024 - VikashVerma2 */

.ourteam {
  display: block;
  text-align: center;
  background-color: #f4f6f9;
  padding-top: 4rem;
  padding: 4% 8%;
}
.team-para {
  font-size: 1.5rem;
  color: #ff4f01;
  line-height: 1.5rem;
  text-transform: capitalize;
}
.ourteam h1 {
  font-size: 3rem;
  color: #131d3b;
  font-weight: 900;
}

.team-hero {
  display: flex;
  justify-content: center;
  margin-top: 4rem;
  /* margin: 4rem; */
}
.team-containor {
  width: 400px;
  height: 540px;
  background-color: white;

  border-radius: 1rem;
  margin: 6%;
}
.team-containor .team-img {
  display: flex;
  justify-content: center;
}
.team-img img {
  width: 200px;
  height: 240px;
  border-radius: 20%;
  margin: 20px;
}

.team-des h2 {
  color: #131d3b;
  font-size: 28px;
  font-weight: 700;
}
.team-des p {
  font-size: 20px;
  font-weight: 200;
  margin: 10px 40px;
}

/*  ------------------- Our Institutes --------------- */
/* Copyright 2024 - VikashVerma2 */

.institutesBG {
  width: 100vw;
  height: 100%;
  background: #131d3b;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 4% 8%;
  display: flex;
}
.institutes-title {
  color: white;
  font-size: 3rem;
  line-height: 25px;
  font-weight: 1000;
  margin-bottom: 2rem;
  margin-top: 2rem;
}
.institues-box1 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.institues-content {
  display: flex;
  align-items: center;
  margin: 2rem 2rem 0 0;
}
.institues-icons img {
  width: 60px;
  height: 60px;
}
.institues-des p {
  color: lavender;
  font-size: 16px;
  margin-left: 10px;
}
.institues-des a {
  text-decoration: none;
}
.institues-des a :hover {
  color: #ff4f01;
  transition: 1 ease-out;
}
.institues-box2 {
  display: flex;
}
.para-des p {
  color: darkgray;
  font-size: 18px;
  margin-top: 1.5rem;
}

/*  ------------------- OurProgram --------------- */
/* Copyright 2024 - VikashVerma2 */

.program-title {
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: whitesmoke;
  padding: 3rem 8%;
}
.program-title h3 {
  color: #ff4f01;
  font-size: 24px;
  font-weight: 200;
  text-transform: uppercase;
}
.program-title h1 {
  color: #131d3b;
  font-size: 3rem;
  font-weight: 1000;
}

.program {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  padding: 3rem 8%;
}
.program-card {
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: whitesmoke;
  padding: 2rem 0 0 0;
  cursor: pointer;
}
.program-card h2 {
  font-size: 20px;
  color: #131d3b;
  font-weight: 900;
}
.program-card img {
  padding: 20px;
  width: 100%;
}
.program-card img:hover {
  transform: scale(1.1);
  transition: 0.4s ease-out;
}

.footerBG {
  background-color: #131d3b;
  color: white;
  padding: 4% 8%;
}
.footer-content img {
  width: 100%;
  margin-bottom: 1rem;
}
.footer-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 160px;
}
.footer-nav ul li {
  display: flex;
  text-decoration: none;
  list-style: none;
  align-items: center;
  gap: 5px;
}
.footer-nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}
.footer-nav ul li a:hover {
  color: #ff4f01;
  transition: 0.4s ease-out;
}

.accessory {
  background-image: radial-gradient(
    closest-side,
    hsla(0, 0%, 50%, 1),
    hsla(0, 0%, 50%, 0) 100%
  );
  position: relative;
  margin: 2rem 0;
}
.accessory:after {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  background-color: hsl(0, 0%, 75%);
  height: 12px;
  width: 12px;
  transform: rotate(45deg);
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 4px 0;
  border: 4px solid hsla(0, 0%, 100%, 0.35);
  background-clip: padding-box;
  box-shadow: -10px 10px 0 hsla(0, 0%, 100%, 0.15),
    10px -10px 0 hsla(0, 0%, 100%, 0.15);
}

.footer-des {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-top: 2rem;
}
.footer-des p {
  font-size: 16px;
  color: darkgray;
}

.footer-icons ul {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  list-style: none;
}
.footer-icons ul li a {
  color: darkgray;
  font-size: 20px;
  margin: 0 10px;
}
.footer-icons ul li a:hover {
  color: #ff4f01;
  transition: 0.4s ease-out;
}
.footer-credit {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}
.footer-credit p {
  font-size: 16px;
  color: darkgray;
}
.footer-credit a {
  color: whitesmoke;
  text-decoration: none;
}
.footer-credit a:hover {
  color: #ff4f01;
  transition: 0.4s ease-out;
}
